<template>
  <div class="pm_div">
    <div class="bg_div">
      <el-table :data="meetingData" v-loading="loading" style="width: 100%">
        <el-table-column prop="meetingId" label=""> </el-table-column>
        <el-table-column prop="meetingName" label="" width="200">
        </el-table-column>
        <el-table-column prop="time" label="" width="120">
        </el-table-column>
        <el-table-column prop="status" label="" width="120">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status === ''" type="info"
              ></el-tag
            >
            <el-tag v-else-if="scope.row.status === ''" type="warning"
              ></el-tag
            >
            <el-tag v-else-if="scope.row.status === ''" type="success"
              ></el-tag
            >
            <el-tag v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="gotoAdvertising(scope.row)"
              v-if="scope.row.status === ''"
            >
              
            </el-button>
            <!-- <el-button
              type="text"
              @click="gotoOrdering(scope.row)"
              v-if="scope.row.status === ''"
            >
              
            </el-button> -->
            <el-button
              type="text"
              @click="gotoOrdering(scope.row)"
              v-if="scope.row.status === ''"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timer: "",
      uid: this.$store.getters.getUser.role.uid,
      loading: false,
      newMeetingData: {
        class: "",
        year: "",
        term: "",
      },
      meetingData: [],
    };
  },
  mounted() {
    this.loadMeeting();
    this.timer = setInterval(() => {
      this.loadMeeting();
    }, 5000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    gotoAdvertising(data) {
      this.$router.push({
        path: "/advertising",
        query: {
          year: data.time,
          meetingId: data.meetingId,
        },
      });
    },
    gotoOrdering(data) {
      this.$router.push({
        path: "/ordering",
        query: {
          meetingId: data.meetingId,
        },
      });
    },
    loadMeeting() {
      this.getRequest("/market/stuGetMeetingList?uid=" + this.uid).then(
        (resp) => {
          if (resp) {
            this.meetingData = resp.data.data;
          }
        }
      );
    },
  },
};
</script>
<style scoped>
.pm_div {
  display: flex;
  flex-direction: column;
  margin-left: 1%;
  margin-right: 2%;
  width: 1000px;
}
.bg_div {
  display: flex;
  flex-direction: column;
  /* background-color: white; */
  padding: 1%;
}
</style>